import Vue from 'vue'
import VueRouter from 'vue-router'

import CourseListingPage from './pages/CourseListingPage.vue'
import CourseCreationPage from './pages/CourseCreationPage.vue'

import NotFound from './pages/NotFound.vue'


Vue.use(VueRouter);
const router=new VueRouter({
    mode:'history',
    routes:[
        { path:'/', redirect:'/courses'},
        { path:'/courses', component : CourseListingPage},
        { path:'/courses/addCourse', component : CourseCreationPage },  
        { path:'/:NotFound(.*)', component : NotFound },
        
    ],

});
export default router;