<template>
  <v-card >
    <v-img :src="course.image" alt="Course Image" height="200px"/>
    <v-card-title>{{ course.name }}</v-card-title>
    <v-card-text style="overflow-y: hidden; height:60px">{{ course.description }}</v-card-text>
    <v-card-actions>
      <v-btn rounded  @click="exploreCourse">...</v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
export default {
  props: {
    course: {
      type: Object,
      required: true,
    },
    
  },
  methods: {
    exploreCourse() {
      this.$emit('explore', this.course);
    },
  },
};
</script>
