<template>
  <div>
    <v-container>
        <h2 class="mt-3 mb-3">Create New Course</h2>
      <v-form @submit.prevent="createCourse">
        
        <v-text-field outlined v-model="courseName" label="Name*" :rules="[v => !!v || 'Please Enter Course Name']"></v-text-field>
        <v-textarea outlined v-model="courseDescription" label="Description" ></v-textarea>
        <v-textarea auto-grow rows="1" outlined v-model="imageUrl" label="Image Url" ></v-textarea>
        <v-card class="mt-4">
          <v-card-title>
            <span>Schedules</span>
          </v-card-title>
         
         <course-schedule v-for="schedule in schedules" :key="schedule.id" :schedule="schedule" />
           </v-card>

   
        <br><v-btn  @click="modalOpen = true"><span class="material-symbols-outlined">add</span>Add Schedule</v-btn><br><br>
      
      <v-dialog v-model="modalOpen" max-width="500px">
   
      <v-card>
        <v-card-title>
          <span >Add new schedule</span>
        </v-card-title>
        <v-card-text>
          <v-form>
            <v-text-field outlined v-model="courseTeacher" label="Teacher"></v-text-field>
            <div v-if="sessions.length > 0">
          <h4>Added Sessions:</h4>
          <ul>
            <li v-for="session in sessions" :key="session.id">{{ session.day }} - {{ session.startTime }} to {{ session.endTime }}</li>
          </ul>
          <br>
        </div>
            <v-btn @click="showNewSessionForm = true" v-if="!showNewSessionForm"><span class="material-symbols-outlined">
add
</span>Add New Session</v-btn>
            <template v-else>
                <v-select outlined v-model="selectedDay" :items="daysOfWeek" label="Select Day*" ></v-select>
                <v-row><v-col cols="12" sm="6"><v-select outlined v-model="selectedStartTime" label="Start Time*" :items="Time"></v-select></v-col>
                <v-col cols="12" sm="6"><v-select  outlined v-model="selectedEndTime" label="End Time*" :items="Time"></v-select></v-col></v-row>
                <v-btn text @click="addSession">Add Session</v-btn>
                <v-btn text @click="showNewSessionForm = false">Cancel</v-btn>
            </template>
            </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn dark color="grey" @click="modalOpen = false">Close</v-btn> <v-btn dark color="green" @click="saveScheduleChanges">Save Changes</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    
    <v-btn dark class="mr-3"><router-link :to="'/courses'">Back</router-link></v-btn><v-btn  color="green" type="submit" @click="createCourse" :disabled="!isFormValid">Create</v-btn>
      </v-form>
    </v-container>
  </div>
</template>

<script>
import apiService from "@/apiService.js";
import CourseSchedule from "@/components/CourseSchedule.vue";

export default {
  components: {
    CourseSchedule,
  },
  data() {
    return {
      courseName: "",
      courseDescription: "",
      courseTeacher:"",
      imageUrl:"",
      schedules:[],
      sessions: [],
      modalOpen: false,
      showNewSessionForm: false,
      selectedDay: null,
      selectedStartTime:null,
      selectedEndTime:null,
      Time: ["1am","2am","3am","4am","5am","6am","7am","8am","9am","10am","11am","12am","1pm","2pm","3pm","4pm","5pm","6pm","7pm","8pm","9pm","10pm","11pm","12pm"],
      daysOfWeek: ["Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday", "Sunday"],
    };
  },
  computed: {
    isFormValid() {
  return this.courseName && this.schedules.length > 0;
},

  },
 
  methods: {
    addSession() {
      if (this.selectedDay && this.selectedStartTime && this.selectedEndTime) {
        const newSession = {
          day: this.selectedDay.substring(0, 3),
          startTime: this.selectedStartTime,
          endTime: this.selectedEndTime,
        };
        this.sessions.push(newSession);
        this.selectedDay = null;
        this.selectedStartTime = null;
        this.selectedEndTime = null;
     
      }
    },
    saveScheduleChanges() {
  if (this.sessions.length > 0) {
    const scheduleId = this.schedules.length + 1;
    this.schedules.push({ id: scheduleId,teacher:this.courseTeacher, sessions: this.sessions });
    this.sessions = [];
    this.courseTeacher="",
    this.modalOpen = false;
  }
    },
    createCourse() {
      if (this.isFormValid) {
        const newCourse = {
          name: this.courseName,
          description: this.courseDescription,
          image:this.imageUrl,
          schedules: this.schedules,
        };
        apiService.addCourse(newCourse);

        this.courseName = "";
        this.courseDescription = "";
        this.schedules = [];
        this.$router.push("/courses");
      }
    },
  },
};
</script>
<style scoped>
  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    border: 1px solid #ccc;
    padding: 10px;
   
  }

a{
  text-decoration: none;
  color:white !important;
}



</style>