<template>
    <v-container fluid>
      <v-btn dark  class="mt-3 mb-3"><router-link :to="'/courses/addCourse'">Add Course</router-link></v-btn><br>
      <h3>Our Courses</h3>
      <v-row>
        <v-col
          cols="12"
          sm="6"
          md="4"
          lg="3"
          v-for="course in courses"
          :key="course.id"
        >
          <course-card :course="course" @explore="openModal" />
        </v-col>
      </v-row>
   
    <v-dialog v-model="dialog" max-width="800">
       <course-modal 
       :course="selectedCourse"
       @close="closeDialog"
      
       ></course-modal>
      </v-dialog>
       </v-container>
</template>

<script>
import CourseCard from '@/components/CourseCard.vue';
import CourseModal from '@/components/CourseModal.vue';
import apiService from '@/apiService.js';

export default {
  components: {
    CourseCard,
    CourseModal,
  },
  data() {
    return {
      courses: [],
      selectedCourse: null,
      dialog: false,
    };
  },
  
  methods: {
    openModal(course) {
      this.selectedCourse = course;
       this.dialog = true;
      
    },
    closeDialog(){
      this.dialog=false;
    }
  },
  created() {
    this.courses = apiService.getCourses();
  },

};
</script>

<style scoped>
a{
  text-decoration: none;
  color:white !important;
}
</style>
